@import "../prevCss/selects2.min.css";
@import "../prevCss/bootstrap.min.css";
@import "../prevCss/fontawesome/css/fontawesome.min";
@import "../prevCss/fontawesome/css/all.min";
@import "../prevCss/boxicons/css/boxicons.min";
@import "../prevCss/dropzone.min.css";
@import "../prevCss/feather.css";
@import "../prevCss/daterangepicker.css";
@import "../prevCss/mainStyles.css";
@import "btns";

@import "null";
@import "mixins";
@import "fonts";

$laptop: 1240px;
$tablet: 991.92px;
$mobile: 767.92px;
$mobileMedium: 575.98px;
$blue: #274D74;
$orange: #C56940;
$black: #011222;
$lGreen: #708990;
$sand: #D5D0CA;
$lGrey: #F2F3F7;
@import "forms";
@import "./layout/header";
@import "./layout/footer";

.layout-off {
  .header {
    display: none;
  }

  .footer {
    display: none;
  }
}

.welcome-login-bg {
  top: 0;
  left: 0;
  position: fixed;
  min-height: 100vh;
  display: flex;
  align-items: center;
  background-color: #274D74;
  @media (max-width: 767.98px) {
    display: none;

  }

  &__photo {
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    right: 0;
    height: 466px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: -1;
  }

  .welcome-login {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
  }

  .mentor-course {
    max-width: 400px;
    margin-top: -250px;

    h1, h2, h3, h4 {
      text-transform: uppercase;
      color: #fff;
      font-weight: 700;
    }

    p {
      color: #fff;

    }
  }
}

.profile-pic {
  width: 100px;
  height: 100px;
  flex-shrink: 0;
  margin-right: 10px;
  border-radius: 50%;
  overflow: hidden;
  @media (max-width: 575px) {
    margin-right: 0;
  }
}

.webinar-card {
  background-color: #F5F4FA;
  border-radius: 10px;
  padding: 20px 20px 48px;

  &--detail {
    padding: 0;
    background-color: unset;

    .webinar-card {
      &__contains,
      &__duration {
        font-size: 14px;
      }

      &__title {
        margin-bottom: 12px;
      }
    }
  }

  .video-thumbnail {
    margin-bottom: 14px;
  }

  &__picture {
    height: 194px;
    margin-bottom: 20px;
    border-radius: 6px;
    overflow: hidden;
    @media (max-width: $mobile) {
      height: 40vw;
      border-radius: 6px;
      overflow: hidden;
      min-height: 150px;
    }


    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    font-size: 18px;
    font-weight: 600;
    line-height: 21.78px;
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 600;
      line-height: 19.36px;
    }
  }

  &__price {
    margin-top: 16px;
  }

  &__new-price {
    font-size: 18px;
    font-weight: 800;
    line-height: 24px;
    white-space: nowrap;
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__old-price {
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    color: #C2C2C2;
    text-decoration: line-through;
    margin-left: 19px;
    white-space: nowrap;
    @media (max-width: $mobile) {
      font-size: 12px;
      line-height: 14px;
      margin-left: 10px;
    }
  }

  &__descr {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.02em;
  }

  &__labels {
    display: flex;
    justify-content: space-between;
    color: #685F78;
    padding-bottom: 18px;
    border-bottom: 1px solid #C8CDD3;
    @media (max-width: $tablet) {
      font-size: 12px;
      line-height: 14.52px;
    }
  }

  &__btn {
    margin-top: 32px;
  }

  &__contains {
    position: relative;
    display: flex;
    align-items: center;

