    }
  }
}

.telegram-news-item {
  padding: 24px 24px 32px;
  border: 1px solid #85A3B1;
  border-radius: 10px;
  overflow: hidden;

  &__top {
    display: flex;
    flex-direction: column;
    margin-bottom: 24px;
  }

  &__link {
    color: $orange;
    font-size: 12px;
    font-weight: 400;
    line-height: 14.52px;
  }

  &__ava {
    display: inline-block;
    width: 64px;
    height: 64px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 40px;
    margin-bottom: 14px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__name {
    font-size: 18px;
    font-weight: 600;
    line-height: 21.78px;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 600;
      line-height: 19.36px;
    }
  }

  &__text {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;

    p + p {
      margin-top: 1em;
    }
  }

  &__more {
    margin-top: 32px;
  }
}

.dashboard-block {

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
    @media (max-width: $mobile) {
      flex-wrap: wrap;
    }

    .dashboard-block__subtitle {
      margin-bottom: 0;
      @media (max-width: $mobile) {
        margin-bottom: 20px;
        width: 100%;
      }
    }
  }

  & + .dashboard-block {
    margin-top: 40px;
    @media (max-width: $tablet) {
      margin-top: 30px;
    }
  }


  .row {
    margin-bottom: -20px;

    & > div {
      margin-bottom: 20px;
    }
  }

  &__subtitle {
    font-size: 18px;
    font-weight: 600;
    line-height: 21.78px;
    margin-bottom: 19px;
    color: #274D74;
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 18.78px;
    }
  }
}

.resource-item {
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 5px 13px 0px #C4D0D933;

  &__picture {
    display: inline-block;
    height: 188px;
    transition: opacity .3s;

    &:hover {
      opacity: .7;
    }

    @media (max-width: $mobileMedium) {
      height: 173px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__info {
    padding: 24px 16px 32px;
  }

  &__title {
    display: inline-block;
    font-size: 18px;
    font-weight: 600;
    line-height: 21.78px;
    margin-bottom: 12px;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 600;
      line-height: 19.36px;
    }

  }

  &__text {
    margin-bottom: 32px;
  }

  &__date {
    position: relative;
    display: flex;
    align-items: center;
    color: #685F78;

