.materials-item {
  display: flex;
  align-items: center;
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;

  & + .materials-item {
    margin-top: 24px;
    @media (max-width: $tablet) {
      margin-top: 16px;
    }
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: $mobileMedium) {
      margin-bottom: 12px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 20px 24px;
    @include hover-effect;
    @media (max-width: $mobileMedium) {
      flex-direction: column;
      padding: 20px 16px;
      text-align: center;
    }
  }

  &__title {
    color: #011222;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.02em;
    margin-left: 16px;
    @media (max-width: $mobileMedium) {
      margin-left: 0;
    }
  }

  &__label {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    color: $orange;
  }

  &__label {
    margin-left: auto;
    @media (max-width: $mobileMedium) {
      margin: 24px auto 0;
    }
  }
}


.sertificat-card {
  &__photo {
    margin-bottom: 13px;

    img {
      width: 100%;
    }
  }

  &__name {
    font-size: 18px;
    font-weight: 600;
    line-height: 21.78px;
    margin-bottom: 12px;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 600;
      line-height: 19.36px;
    }
  }

  &__descr {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.02em;
  }

  &__btn {
    margin-top: 15px;
  }
}

.dash-pagination {
  margin-top: 25px;
  @media (max-width: $mobile) {
    margin-bottom: 20px;
  }

  &__list {
    display: flex;
    justify-content: center;
  }
}

.prod-list-wrapper {
  margin-bottom: -20px;

  & > div {
    margin-bottom: 20px;
  }
}

.cou-info {
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.web-badge {
  margin-left: auto;
}

.course-card ul span.course-card__icon {
  margin-left: 0;
}

.expert-card {
  border-radius: 10px;
  overflow: hidden;

  a {
    @include hover-effect;
  }

  &__picture {
    display: flex;
    height: 320px;
    @media (max-width: $laptop) {
      height: 35vw;
    }
    @media (max-width: $mobile) {
      height: 58vw;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__info {
    background: #F2F3F7;
    padding: 35px 35px 40px;
    text-align: center;
  }

  &__name {
    font-size: 18px;
    font-weight: 600;
    line-height: 21.78px;
    margin-bottom: 12px;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 600;
      line-height: 19.36px;
    }
  }

  &__descr {
    margin-bottom: 12px;
  }

  &__cources-count {
    position: relative;
    color: #685F78;

    &::before {
      content: '';
      position: relative;
      top: 2px;
      margin-top: 5px;
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='14' height='15' viewBox='0 0 14 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M13.1249 10.5938C13.1249 10.8125 13.0155 11.0039 12.8788 11.1133C12.7421 11.5234 12.7421 12.7266 12.8788 13.1641C13.0155 13.2734 13.1249 13.4648 13.1249 13.6562V14.0938C13.1249 14.4766 12.8241 14.75 12.4687 14.75H3.4999C2.05068 14.75 0.874902 13.5742 0.874902 12.125V3.375C0.874902 1.92578 2.05068 0.75 3.4999 0.75H12.4687C12.8241 0.75 13.1249 1.05078 13.1249 1.40625V10.5938ZM4.3749 4.41406V4.96094C4.3749 5.07031 4.42959 5.125 4.53896 5.125H10.3358C10.4179 5.125 10.4999 5.07031 10.4999 4.96094V4.41406C10.4999 4.33203 10.4179 4.25 10.3358 4.25H4.53896C4.42959 4.25 4.3749 4.33203 4.3749 4.41406ZM4.3749 6.16406V6.71094C4.3749 6.82031 4.42959 6.875 4.53896 6.875H10.3358C10.4179 6.875 10.4999 6.82031 10.4999 6.71094V6.16406C10.4999 6.08203 10.4179 6 10.3358 6H4.53896C4.42959 6 4.3749 6.08203 4.3749 6.16406ZM11.2929 13C11.2382 12.5352 11.2382 11.7422 11.2929 11.25H3.4999C3.00771 11.25 2.6249 11.6602 2.6249 12.125C2.6249 12.6172 3.00771 13 3.4999 13H11.2929Z' fill='%23C56940'/%3e%3c/svg%3e ");
      width: 14px;
      height: 15px;
      display: inline-block;
    }
  }
}

.instructor-sec {
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px -20px;
    @media (max-width: $mobile) {
      margin: 0 0 -20px;
    }

    .expert-card {
      width: calc(50% - 40px);
      margin: 0 20px 20px;
      @media (max-width: $mobile) {
        width: 100%;
        margin: 0 0 20px;
      }
    }
  }
}

.card-body__duration-of {
  font-size: 16px;
  color: #002058;
  font-weight: 500;
}

.sidebar-sec {
  &--progress {
    margin-top: 0;
