.navi-btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  background-color: #C56940;
  color: #fff;
  border-radius: 8px;
  height: 46px;
  text-align: center;
  padding: 13px 24px;
  outline:none;
  border:none;
  cursor: pointer;
  transition: background-color .3s, opacity .3s;

  &--mini{
    height: 32px;
    padding:5px 24px;
  }

  &:hover {
    opacity: .7;
    color:#fff;
  }

  &--border {
    border: 1px solid #C56940;
    background-color: transparent;
    color: #C56940;
  }
  &--blue-border {
    border: 1px solid #274D74;
    background-color: transparent;
    color: #274D74;
    &:hover{
      color:#274D74;
    }
  }

  &--light{
    background-color: #FFFFFF;
    color:#011222;
    &:hover{
      color:#fff;
    }
  }

  &--green{
    background: #277433;
    color:#fff;
    &:hover{
      color:#fff;
    }
  }

  &--blue {
    border: 1px solid #274D74;
    background-color: #274D74;
    color: #fff;

    &:hover{
      color:#fff;
    }

    &.btn--border {
      background-color: #fff;
      color: #274D74;
    }
  }
}
