.header,
.footer {


  html,
  body {
    height: 100%;
  }

  body {
    line-height: 1;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }


  a {
    color: inherit;
    text-decoration: none;
  }


  img {
    vertical-align: top;
  }

}
